
.swiper-container{
	width: 100%;
}
.swiper-slide{
	position: relative;
}
.swiper-btn1,.swiper-btn2{
	position: absolute;
	width:154px;
	height: 50px;
	text-align: center;
	line-height: 50px;
	font-size: 22px;
	bottom:16%;
}
.swiper-btn1{
	background: #108ee9;
	left:40%;
}
.swiper-btn2{
	background: #e79700;
	left:52%;
}
a{
	color: #fff;
	text-decoration: none;
}
.swiper-slide img{
	width: 100%;
	display: block;
}



